
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { wxPay } from "@/utils/wx_sdk.js";
export default {
components: {
XHeader
},
data() {
return {
isSelect: false,
methodList:[],
methodType: {
"pay_code": 2,
"pay_name": "微信",
"icon": 'http://img.xiepinhui.com.cn/mobile/bank/wxpay.png'
},
money:'',
activeTab:'',
choose_list: [],
};
},
created() {

},
mounted() {
this.rechargeTab();
},
methods: {
async rechargeTab(){

let data = {
plat: 3,
token: this.token,
account: this.account
}
const [err, res] = await api.rechargeMoneyTab(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
this.choose_list = res.data.choose_list;
}
},
getCurrent(e){
this.activeTab=e;
console.log(this.activeTab)
this.money=e;
},
showBankList() {

this.isSelect = !this.isSelect;
this.rechargeWayList();
},
async rechargeWayList() {
let data = {
plat: 3,
token: this.token,
account: this.account,
scene_id: 3
};
const [err, res] = await api.rechargeWayList(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
var list_obj = res.data.list;
this.methodList = list_obj;
}
},
selectType(e) {
console.log(e)
const index = e.target.dataset.index;
console.log(index)
const data = this.methodList[index];
this.methodType=data;
this.showBankList()
},
linkToBankCard(){

},
async goToRecharge() {//offlineStoreRecharge
var that=this;
let data = {
plat: 3,
token: this.token,
account: this.account,
openid: this.user.openid,
recharge_money: this.money,
pay_code: 2,//1支付宝，2微信
};
const [err, res] = await api.onlineRecharge(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
console.log('2000:',res)
let success = res =>{

}
let fail = err =>{

}
console.log(res.data.paystr)
wxPay(that,{...res.data.paystr,success,fail})
}

},
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



